.wk-btn {
  border-radius: 3px;
  /* box-shadow : 0 1px 1px rgba(255, 255, 255, 0.4) inset ; */
  color: #000;
  display: inline-block;
  font-size: 1.4rem ;
  line-height: 170% ;
  padding: .5rem 1rem ;
  text-align: center ;
}
.wk-btn.wk-btn-lg {
  font-size: 1.6rem ;
}
.wk-btn.wk-btn-sm {
  font-size: 1.2rem ;
}
.wk-btn.wk-btn-xs {
  font-size: 1rem ;
  -webkit-transform: scale(0.84, 0.84);
}
.wk-btn.wk-btn-transparent {
  background-color: transparent ;
  border: 1px solid transparent ;
  outline: none ;
}
.wk-btn.wk-btn-block {
  display: block ;
  width: 100% ;
}
.wk-btn.wk-btn-success {
  background-image: -webkit-linear-gradient(top, #04be02, 20%, #4cae4c, 100%);
