/*-----------------------------------------------------------------------------------------------------------------------------------------------------------------------
 1. 项目名称：yfyk2h5
 2. 文件名称：src -> less -> components -> modal.less (modal框)
 3. 作者：zhaohuagang@lifang.com
 -----------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
.wkzf-modal {
  background-color: #ffffff;
  box-shadow: 0 4px 23px 5px rgba(0, 0, 0, 0.2), 0 2px 6px rgba(0, 0, 0, 0.15);
  border-radius: 3px;
  display: none ;
  min-width: 17rem ;
  position: fixed;
  max-width: 60% ;
  z-index: 9998 ;
}
.wkzf-modal .modal-header {
  font-size: 1.6rem ;
  line-height: 3rem ;
  padding: 1.5rem 0 .75rem ;
  text-align: center ;
}
.wkzf-modal .modal-body {
  color: #999;
  font-size: 1.4rem ;
  line-height: 150% ;
  padding: 0 .75rem ;
  text-align: center ;
}
.wkzf-modal .modal-footer {
  border-top: 1px solid #eee;
  margin-top: 1.5rem ;
  text-align: center ;
}
.wkzf-modal .modal-footer .wkzf-btn {
  border-left: 1px solid #eee;
}
.wkzf-modal .modal-footer .wkzf-btn:nth-child(1) {
  border-left: none ;
}
.wkzf-modal-mask {
  background-color: rgba(0, 0, 0, 0.65);
  display: none ;
  height: 100% ;
  left: 0 ;
  position: fixed ;
  top: 0 ;
  width: 100% ;
  z-index: 9997 ;
}
html,
body,
#app {
  background-color: #effafe ;
  height: 100% ;
}



