  line-height: 2rem ;
  padding: .5rem 1rem ;
  position: fixed ;
}
dl[data-v-0eab003a] {
  padding: 0 1rem 3rem 1rem ;
}
dl dt ul .desc[data-v-0eab003a] {
  background-image: url(../../assets/professional/caption-bg.png);
  background-size: 100% auto ;
  font-size: 1.7rem ;
  line-height: 2rem ;
  padding: 0 1rem ;
}
dl dd[data-v-0eab003a] {
  border: 1px solid #4e4e4e ;
