  background-color: #fff ;
  font-size: 1.7rem ;
  line-height: 2rem ;
  margin: 1rem .5rem 0 .5rem ;
  padding: .5rem .5rem 1rem .5rem ;
  text-align: center ;
}
dl dd.checked[data-v-0eab003a] {
  background-image: linear-gradient(to right, #FFE892, #FFBA47);
  color: #fff ;
}



